@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css?family=Poppins&display=swap);
:root {
    --light-blue: #eef7fa;
    --blue: rgb(70, 120, 160);

    --smoke: #f0f0f0;
    --gray: #e8e8e8;

    --font1: 'Lato', sans-serif;
    --font2: 'Poppins', sans-serif;

    --navBar-height: 45px;
}

body,
html {
    background-color: black;
    margin: 0;
    padding: 0;
    max-width: 100%;
    font-family: 'Lato', sans-serif;
    font-family: var(--font1);
    scroll-behavior: smooth;
}

button {
    background-color: black;
    border: 1px black solid;
    color: white;
    -webkit-transition: all 0.1s ease-in-out;
    transition: all 0.1s ease-in-out;
}

button:focus {
    outline: none;
}

button:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.tag {
    display: inline-block;
    font-size: 12px;
    border-radius: 8px;
    padding: 3px 6px;
    margin: 3px 6px 3px 0;
    cursor: default;
}

.languageTag {
    background-color: lightsalmon;
}

.toolTag {
    background-color: lightblue;
}

@media (max-width: 575px) {
    .tag {
        font-size: 10px;
        border-radius: 8px;
        padding: 1px 6px;
    }
}

.navBar {
    position: fixed;
    top: 0;
    width: 100%;
    background-color: black;
    list-style-type: none;
    height: var(--navBar-height);
    z-index: 3;
    border-bottom: 3px var(--blue) solid;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.navBar .navBarContainer {
    position: absolute;
    left: 0;
    right: 0;
    top: 55%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}

.navBar .logo {
    font-family: var(--font2);
    float: left;
    font-weight: bolder;
}

.navBar .navBarRight {
    margin: 0;
    padding: 0;
    text-align: right;
}

.navBar .navBarRight li {
    margin-left: 15px;
}

.navBar .navItem {
    display: inline;
}

.navBar .navItem a {
    display: inline-block;
    color: white;
    text-decoration: none;
    -webkit-transition: all 0.1s ease-in-out;
    transition: all 0.1s ease-in-out;
}

.navBar .navItem a:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.navBar .navItemActive {
    color: var(--blue) !important;
    font-weight: bolder;
}

.footer {
    padding: 20px;
    background-color: black;
    color: gray;
    text-align: center;
    z-index: 3;
}

.footer .footerIcon {
    display: inline-block;
    color: gray;
    padding: 0 35px;
    font-size: 30px;
    -webkit-transition: all 0.1s ease-in-out;
    transition: all 0.1s ease-in-out;
}

.footer .footerIcon:hover {
    color: var(--blue);
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

.footer .footerCopyright {
    padding-top: 10px;
}

@media (max-width: 575px) {
    .footer .footerIcon {
        padding: 0 24px;
        font-size: 25px;
    }
}

.loadingScreen {
    background-color: var(--light-blue);
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.spinner {
    font-size: 100px;
    color: var(--blue);
    -webkit-animation: spin 1s linear infinite;
            animation: spin 1s linear infinite;
}

@-webkit-keyframes spin {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes spin {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
.gone {
    display: none;
}

.home {
    color: black;
    background-color: white;
}

.home .landingScreen {
    height: 100vh;
    width: 100%;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.home .backgroundVideoWrap {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    overflow: hidden;
}

.home .backgroundVideoWrap video {
    min-width: 100%;
    min-height: 100%;
}

.home .videoOverlay {
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    z-index: 1;
    background: white;
    opacity: 0.3;
}

.home .landingContent {
    padding-bottom: 100px;
    width: 70%;
    z-index: 2;
}

.home .landingTitle {
    font-family: var(--font2);
    font-size: 80px;
    font-weight: bolder;
}

.home .landingSubtitle {
    font-family: var(--font2);
    font-size: 25px;
}

.home .landingButtons {
    padding-top: 30px;
    width: 85%;
    margin: 0 auto;
}

.home .landingButton {
    font-size: 20px;
    margin-top: 30px;
    padding: 10px 20px;
    border-radius: 5%;
}

.home .landingArrow {
    font-size: 50px;
    -webkit-animation: MoveUpDown 1s linear infinite;
            animation: MoveUpDown 1s linear infinite;
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
    z-index: 2;
}

.home .landingArrow button {
    color: black;
    background: none;
    border: none;
}

.home .landingArrow button:focus {
    outline: none;
}

@-webkit-keyframes MoveUpDown {
    0%,
    100% {
        bottom: 130px;
    }
    50% {
        bottom: 140px;
    }
}

@keyframes MoveUpDown {
    0%,
    100% {
        bottom: 130px;
    }
    50% {
        bottom: 140px;
    }
}

@media (max-width: 1199px) {
    .home .landingButtons {
        width: 100%;
    }
}

@media (max-width: 991px) {
    .home .landingButton {
        font-size: 18px;
        padding: 5px 15px;
        margin-top: 15px;
    }
}

@media (max-width: 767px) {
    .home .landingTitle {
        font-size: 60px;
    }
}

@media (max-width: 575px) {
    .home .landingContent {
        width: 90%;
    }

    .home .landingTitle {
        font-size: 48px;
    }

    .home .landingSubtitle {
        font-size: 22px;
    }
}

@media (max-height: 715px) {
    .home .landingContent {
        padding-bottom: 0;
    }

    .home .landingArrow {
        visibility: hidden;
    }
}

.about {
    padding: 70px 0 20px 0;
    background-color: var(--gray);
    color: black;
    font-size: 20px;
}

.about .portrait {
    padding-bottom: 10px;
}
.about .portrait img {
    width: 100%;
}

.about .aboutIntro {
    font-size: 22px;
}

.about .aboutTitle {
    font-weight: bolder;
    padding-top: 15px;
    font-size: 20px;
}

.about .skillTitle {
    margin: 5px 15px 0 15px;
    text-align: center;
    border-bottom: 1px solid black;
}

.about .skillRow {
    padding: 10px 10px 0 10px;
}

.about .skillBlock {
    text-align: center;
    font-size: 11px;
}

.about .skillIcon {
    max-width: 100%;
    height: 40px;
    padding-bottom: 5px;
}

@media (max-width: 991px) {
    .about {
        font-size: 18px;
    }

    .about .aboutIntro {
        font-size: 18px;
    }
}

@media (max-width: 575px) {
    .about .skillIcon {
        height: 35px;
    }
    .about .moreAboutInfo {
        text-align: center;
        font-size: 15px;
    }
}

.experience {
    margin-top: var(--navBar-height);
    background-color: white;
    padding: 10px 0;
    color: black;
    font-size: 20px;
}

.experience .experienceRow {
    background-color: var(--smoke);
    padding: 20px;
    margin: 10px 0 20px 0;
}

.experience .experienceRow:hover {
    background-color: var(--light-blue);
}

.experience .experienceRowTitle {
    font-size: 30px;
    text-decoration: none !important;
    color: inherit;
}
.experience .experienceRowTitle:hover {
    color: inherit;
}

.experience .experienceRowRight {
    position: relative;
}

.experience .experienceImage {
    max-width: 65%;
    max-height: 65%;

    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

@media (max-width: 991px) {
    .experience .experienceImage {
        max-width: 70%;
        max-height: 70%;
    }
}

@media (max-width: 767px) {
    .experience {
        font-size: 17px;
    }

    /* Remove vertical centering */
    .experience .experienceRow {
        text-align: center;
    }

    .experience .experienceImage {
        max-height: 120px;

        position: relative;
        left: auto;
        top: auto;
        -webkit-transform: none;
                transform: none;
    }
}

@media (max-width: 575px) {
    .experience {
        font-size: 15px;
    }
    .experience .experienceRowTitle {
        font-size: 25px;
    }
    .experience .experienceImage {
        max-width: 90%;
    }
}

.ReactModal__Overlay {
    z-index: 20;
}

.ReactModal__Content {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.imageCarousel {
    overflow-y: hidden;
    overflow-x: hidden;
}

.imageCarousel .projectImage {
    max-width: 100%;
    max-height: 85vh;
    margin-left: auto;
    margin-right: auto;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.imageCarousel .slideControl {
    position: absolute;
    outline: none;
    padding: 15px;
    border: none;
    background-color: rgb(0, 0, 0, 0.4);
    color: white;
    font-size: 50px;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}

.imageCarousel .previousButton {
    left: 3vw;
}

.imageCarousel .nextButton {
    right: 3vw;
}

.imageCarousel .imagePage {
    position: absolute;
    padding: 3px 10px;
    background-color: rgb(0, 0, 0, 0.5);
    border-radius: 15%;
    color: white;
    right: 3vw;
    top: 3vh;
}

.projects {
    margin-top: var(--navBar-height);
    padding: 10px 0 0px 0;
    background-color: white;
    color: black;
    font-size: 20px;
}

.projects .projectRow {
    background-color: var(--smoke);
    padding: 20px;
    margin: 10px 0 20px 0;
}

.projects .projectRow:hover {
    background-color: var(--light-blue);
}

.projects .projectRowTitle {
    font-size: 30px;
}

.projects .projectImageAndButtons {
    width: 100%;
    max-height: 100%;
    text-align: center;
}

.projects .projectImage {
    max-width: 80%;
    cursor: pointer;
    border: 2px solid var(--blue);
}

.projects .projectButtons {
    margin-top: 10px;
}

.projects .projectButtons button {
    font-size: 15px;
    padding: 5px 15px;
    margin: 0 3%;
    border-radius: 10px;
    min-width: 22%;
}

.projects .moreProjects {
    background-color: var(--smoke);
    padding: 20px;
    margin: 20px 0 0 0;
    text-align: center;
}

@media (max-width: 991px) {
    /* Vertically Center image and buttons at this screen width */
    .projects .projectRowRight {
        position: relative;
    }

    .projects .projectImageAndButtons {
        position: absolute;
        top: 50%;
        left: 50%;
        -webkit-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
    }

    .projects .projectImage {
        max-width: 90%;
    }
}

@media (max-width: 767px) {
    .projects {
        font-size: 17px;
    }

    /* Remove vertical centering */
    .projects .projectRow {
        text-align: center;
    }

    .projects .projectImageAndButtons {
        position: relative;
        left: auto;
        top: auto;
        -webkit-transform: none;
                transform: none;
    }

    .projects .projectImage {
        max-width: 70%;
    }
}

@media (max-width: 575px) {
    .projects {
        font-size: 15px;
    }
    .projects .projectRowTitle {
        font-size: 25px;
    }
    .projects .projectImage {
        max-width: 90%;
    }
}

